import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import ServicesPage from './ServicesPage';
import PortfolioPage from './PortfolioPage';
import AboutPage from './AboutPage';
import ContactPage from './ContactPage';
import { importAllImages } from '../utils/images';
import BlurStepper from '../components/BlurStepper';
import { makeStyles } from '@material-ui/core';
import AOS from 'aos';

const images = importAllImages(require.context('../media/banner', false, /\.(png|jpe?g|svg)$/));
const steps = images.map((image) => ({ image, label: '' }));

const ORIGINAL_WINDOW_ON_SCROLL = window.onscroll;

const useStyles = makeStyles((theme) => ({
  stretchContainer: {
    height: '100vh' // To not show anything other than the blur stepper before the user scrolls
  }
}));

const HomePage = () => {
  const classes = useStyles();
  const [offset, setOffset] = useState(0);

  useEffect(() => {
    window.onscroll = () => {
      setOffset(window.pageYOffset)
    }
    return () => {
      window.onscroll = ORIGINAL_WINDOW_ON_SCROLL;
    };
  }, []);

  const atTop = offset === 0;
  useEffect(() => {
    AOS.refreshHard();
  }, [atTop]);

  return (
    <div>
      <div className={classNames({ [classes.stretchContainer]: atTop })}>
        <BlurStepper steps={steps} />
      </div>
      <div data-aos="fade-up"><ServicesPage /></div>
      <div data-aos="fade-up"><PortfolioPage /></div>
      <div data-aos="fade-up"><AboutPage /></div>
      <div data-aos="fade-up"><ContactPage /></div>
    </div>
  );
};

export default HomePage;